import React, { Fragment } from "react";

import {
  Grid,
  FormControl,
  InputLabel,
  OutlinedInput
} from "@material-ui/core";

function OverTimeData(props) {
  const overTimeRate = props.overTimeRate;
  const setOverTimeRate = props.setOverTimeRate;
  const avgOverTimeHours = props.avgOverTimeHours;
  const setAvgOverTimeHours = props.setAvgOverTimeHours;
  const shiftDiffRate = props.shiftDiffRate;
  const setShiftDiffRate = props.setShiftDiffRate;
  const shiftDiffHours = props.shiftDiffHours;
  const setShiftDiffHours = props.setShiftDiffHours;

  return (
    <Fragment>
      <Grid item>
        <FormControl variant="outlined">
          <InputLabel htmlFor="otRate">Overtime Rate</InputLabel>
          <OutlinedInput
            id="otRate"
            label="Overtime Rate"
            type="text"
            value={overTimeRate}
            onChange={e => setOverTimeRate(e.target.value)}
          />
        </FormControl>
      </Grid>
      <Grid item>
        <FormControl variant="outlined">
          <InputLabel htmlFor="avg-ot">Avg OT Hours Per Week</InputLabel>
          <OutlinedInput
            id="avg-ot"
            label="Avg OT Hours Per Week"
            type="text"
            value={avgOverTimeHours}
            onChange={e => setAvgOverTimeHours(e.target.value)}
          />
        </FormControl>
      </Grid>
      <Grid item>
        <FormControl fullWidth variant="outlined">
          <InputLabel htmlFor="shiftDiffRate">
            Shift Differential Rate
          </InputLabel>
          <OutlinedInput
            id="shiftDiffRate"
            label="Shift Differential Rate"
            type="text"
            value={shiftDiffRate}
            onChange={e => setShiftDiffRate(e.target.value)}
          />
        </FormControl>
      </Grid>
      <Grid item>
        <FormControl fullWidth variant="outlined">
          <InputLabel htmlFor="shiftDiffHours">
            Shift Diff Hours Per Week
          </InputLabel>
          <OutlinedInput
            id="shiftDiffHours"
            label="Shift Diff Hours Per Week"
            type="text"
            value={shiftDiffHours}
            onChange={e => setShiftDiffHours(e.target.value)}
          />
        </FormControl>
      </Grid>
    </Fragment>
  );
}

export default OverTimeData;
