import React from "react";
import Paper from "@material-ui/core/Paper";

function HourlyCalc(props) {
  return (
    <div>
      <h3 align="center">Hourly Calculations</h3>
      <Paper
        align="right"
        style={{
          margin: "0 30%",
          padding: "10px",
          background: "#fffde7"
        }}
      >
        <span>
          {props.currentWage} *
          <br />
        </span>
        <span>
          {props.hoursPerWeek} *
          <br />
        </span>
        <span>
          52 *
          <br />
        </span>

        <span>
          {props.currentWage * props.hoursPerWeek * 52} =
          <br />
        </span>
      </Paper>
    </div>
  );
}

export default HourlyCalc;
